
/**
 * Header component (homepage) : wide + breakpoints.
 */

@media (--wide) {

  .c-header {
    padding-bottom: 50rem;
  }
    .c-header__inner-wrap {
      position: static;
      left: 0;
      width: auto;
    }

  .c-header__menu {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: -2em;
    width: 100%;
  }
    .c-header__menu > .o-grid__item.c-menu-main__item,
    .c-header__menu > .o-grid__item.c-menu-main__item:hover {
      margin: 0;
    }

  .c-header__title {
    top: 1.25rem;
  }

  .c-header__nav {
    left: 51%;
    padding-top: 16%;
    width: 49%;
  }

  .c-header__part2 {
    width: auto;
  }
}
